* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  color: rgb(22, 21, 21);
  font-weight: 550;
}

button {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
  border: none;
  border-radius: 20px;
  /* height: 5rem;
  width: 25rem; */
  padding: 0.7rem;
  color: white;
  background-color: #7852c4;
}
button:active {
  background-color: #8661d1;
  box-shadow: 0 7px #967cc9;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.todo-btn {
  margin-bottom: 30px;
}

.form-container button {
  margin: 2rem auto;
}
.todo-Container {
  background-image: linear-gradient(
    to right,
    rgb(112, 242, 231),
    rgb(255, 148, 114)
  );
  min-height: 100vh;
  height: -webkit-max-content;
  height: max-content;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  padding-top: 2rem;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo-data {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.todo-btn-container {
  display: flex;
  flex-direction: column;
}
.todo-list-block li {
  display: flex;
  width: 100vw;
  justify-content: space-evenly;
  align-items: center;
}

p {
  margin: 10px 0;
  font-size: 25px;
}

hr {
  width: 90%;
  margin: 0 auto;
  margin: 20px;
  border: 1px solid rgb(22, 21, 21);
}
.input {
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
  border: none;
  border-radius: 5px;
  padding: 0.7rem 1rem;
  margin: 0.5rem;
}
.update-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.87);
}
.update-container .label {
  color: white;
  margin: 2rem 10px;
}
